@import url(https://fonts.googleapis.com/css2?family=Poppins:ital,wght@0,100;0,200;0,300;0,400;0,500;0,600;0,700;0,800;0,900;1,100;1,200;1,300;1,400;1,500;1,600;1,700;1,800;1,900&display=swap);
body {
  margin: 0;
  font-family: 'Poppins', sans-serif;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.show {
  display: block !important;
}

.hide {
  display: none !important;
}

body {
  background: radial-gradient(circle, rgba(47, 193, 140, 1) 20%, rgb(3, 107, 82) 100%);
  background-color: rgb(47, 193, 140);
}

* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

.mainLogin {
  align-items: center;
  display: flex;
  flex-direction: column;
  padding: 50px 0;
}

.triviaImg {
  border-bottom: 2px solid black;
  padding: 20px 0;
  width: 400px;
}

.containerInputs {
  align-items: center;
  display: flex;
  flex-direction: column;
  margin-top: 10px;
}

.containerInputs h2 {
  font-size: 35px;
}

.userInputs {
  align-items: baseline;
  display: flex;
  flex-direction: column;
}

.userInputs label {
  align-items: center;
  display: flex;
  padding: 8px 0;
}

.userInputs input {
  background-color: rgb(226, 226, 226);
  border: none;
  border-bottom: 2px solid black;
  border-radius: 3px;
  box-shadow: 0 0 10px 0 rgb(3, 107, 82);
  margin-left: 5px;
  padding: 5px 25px;
}

.userInputs input:hover {
  background-color: rgb(243, 243, 243);
  transition: 0.25s;
}

.userButtons {
  display: flex;
  justify-content: space-evenly;
  margin: 10px 0;
  width: 100%;
}

.userButtons button {
  background-color: rgb(65, 25, 127);
  border: 0;
  border-radius: 5px;
  box-shadow: 0 0 10px 0 rgb(3, 107, 82);
  color: white;
  padding: 10px 25px;
  transition: 0.25s;
}

.userButtons button:hover {
  background-color: rgb(32, 12, 61);
  cursor: pointer;
}

header {
  align-items: center;
  background-color: rgb(65, 25, 127);
  border-bottom: 1px solid rgba(0, 0, 0, 0.6);
  color: white;
  display: flex;
  font-family: monospace;
  justify-content: space-around;
  padding: 5px;
}

header div {
  align-items: center;
  display: flex;
}

header div img {
  border-radius: 50%;
  box-shadow: 0 13px 19px -10px rgba(0, 0, 0, 0.75);
  width: 70px;
}

.logoTrivia {
  width: 250px;
}

header p {
  /* background: linear-gradient(0deg, #036b52 5%, #2fc18c 89%);
  background-clip: text;
  color: transparent; */
  color: white;
  font-size: 30px;
  margin-left: 10px;
  text-shadow: 0 0 90px rgba(255, 255, 255, 0.99) , 0 0 32px rgba(255, 255, 255, 0.99);

}

.score-points {
  margin-bottom: 1px solid white;
}

.highlightedText {
  font-weight: 600;
}

.alternativeButton, .correctAnswer, .wrongAnswer {
  background-color: #006dfb;
  border: none;
  border-radius: 10px;
  box-shadow: 0 0 15px 2px rgba(0, 0, 0, 0.55);
  color: white;
  font-size: 20px;
  margin: 10px;
  padding: 75px 45px;
  transition: 0.5s;
  width: 45%;
}

.alternativeButton:hover {
  box-shadow: 0 0 20px 0 rgb(6, 240, 15);
  cursor: pointer;
}

.correctAnswer {
  box-shadow: 0 0 20px 0 rgb(6, 240, 15);
}

.wrongAnswer {
  box-shadow: 0 0 20px 0 red;
}

.card {
  align-items: center;
  background-color: rgba(255, 255, 255, 0.9);
  border-radius: 10px;
  box-shadow: 0 0 10px 10px rgba(0, 0, 0, 0.25);
  color: black;
  display: flex;
  flex-direction: column;
  margin: 35px auto 0;
  padding: 10px;
  text-align: center;
  transition: 0.7s;
  width: 45%;
}

.card:hover {
  background-color: white;
}

.card h1 {
  font-size: 30px;
}

.card h2, h4 {
  font-size: 20px;
  font-weight: 500;
}

.card h3 {
  font-size: 30px;
  margin-top: 15px;
  text-shadow: 0 4px 4px rgba(0, 0, 0, 0.25);
}

.alternative-box {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  margin-top: 30px;
}

.container-info {
  text-align: center;
}

.container-timer {
  align-items: center;
  display: flex;
  justify-content: space-evenly;
  margin: 10px 0;
}

.buttonNext {
  background-color: #2fc18c;
  border: none;
  border-radius: 15px;
  box-shadow: 0 4px 4px rgba(0, 0, 0, 0.4);
  color: white;
  font-size: 20px;
  font-weight: 600;
  height: 50px;
  width: 150px;
}

.buttonNext:hover {
  cursor: pointer;
}

.arrow {
  font-size: 35px;
  transition: 0.5s;
}

.arrow:hover {
  font-size: 43px;
}

.rotate {
  animation: rotation 10s infinite linear;
  filter: drop-shadow(5px 5px 5px rgba(0, 0, 0, 0.6));
}

@keyframes rotation {

  from {
    transform: rotate(0deg);
  }

  to {
    transform: rotate(359deg);
  }
}

.box-timer img {
  margin-right: 20px;
  max-width: 40%;
}

.box-timer {
  align-items: center;
  display: flex;
  width: 20%;
}

.box-timer p {
  font-size: 40px;
  text-shadow: 0 4px 4px rgba(0, 0, 0, 0.25);
}

